"use client"
import { IcusRegistration } from "@/lib/slice/auth/cus.auth.types"
import { ChangeEvent, FormEvent, useEffect, useState } from "react"
import { ToastContainer } from "react-toastify"
import { Eye, EyeOff } from 'lucide-react'
import Link from 'next/link'
import { useAppDispatch, useAppSelector } from "@/lib/global/hooks"
import { cusRegistration, setStatus } from "@/lib/slice/auth/cus.auth.slice"
import { STATUS } from "@/lib/global/status"
import { useRouter } from "next/navigation"





const Register = () => {
  const router = useRouter()
  const { status } = useAppSelector((store) => store.cusAuth)
  const dispatch = useAppDispatch()

  const [showPassword, setShowPassword] = useState(false)

  const [regData, setRegData] = useState<IcusRegistration>({
    username: "",
    email: "",
    password: "",
    phone_number: "",

    address: {
      street: "",
      city: "",
      ward: "",
      landmark: "",
    },
  })

  useEffect(() => {
    if (status === STATUS.SUCCESS) {
      router.push('/login')
      dispatch(setStatus(STATUS.LOADING))
    }

  }, [status, router, dispatch])

  useEffect(() => {
    localStorage.removeItem("token")
    dispatch(setStatus(STATUS.LOADING))
  }, [dispatch])


  const onChangeData = (e: ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target

    if (["street", "city", "ward", "landmark"].includes(name)) {
      setRegData((prev) => ({
        ...prev,
        address: {
          ...prev.address,
          [name]: value,
        },
      }));
    } else {
      setRegData((prev) => ({
        ...prev,
        [name]: value,
      }));
    }

  }



  const onSubmitData = (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault()

    dispatch(cusRegistration(regData))

  }
  return <div className="flex items-center justify-center min-h-screen bg-yellow-400">
    <div className="bg-white p-8 rounded-2xl shadow-lg w-full max-w-lg min-h-100 ">
      <h2 className="text-2xl font-semibold text-center mb-6">Sign In</h2>
      <form className="space-y-4 space-x-4">
        <div className="username inline-block">
          <input onChange={onChangeData} type="text" placeholder="Username" name="username" value={regData.username} className=" border-0 hover:border hover:border-gray-400 rounded-2xl text-center px-2 py-2 outline-0 shadow-2xl hover:shadow-gray-400 bg-transparent w-48" />

        </div>

        <div className="username inline-block">
          <input onChange={onChangeData} type="text" placeholder="Password" name="password" value={regData.username} className=" border-0 hover:border hover:border-gray-400 rounded-2xl text-center px-2 py-2 outline-0 shadow-2xl hover:shadow-gray-400 bg-transparent w-48" />

        </div>
      </form>
    </div>
  </div>






}

export default Register
